import { api2FA, apiIsLoggedIn } from '../../api.js';
import { Form, Button } from 'react-bootstrap';
import { useState } from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';

function MFAForm(props) {
    const [code, setCode] = useState('');
    const doLogin = (event) => {
        event.preventDefault();
        props.setLoading(true);
        api2FA(code).then((res) => {
            if (res) {
                apiIsLoggedIn().then((res) => {
                    if (res.status == true) {
                        props.setLoading(false);
                        props.setMFAOk(res);
                        props.showSuccess("Codice corretto!")
                    } else {
                        props.setLoading(false);
                        props.showError("Codice errato!");
                    }
                })

            } else {
                props.setLoading(false);
                props.showError("Codice errato!");
            }
        })
    }

    return (
        <Form onSubmit={doLogin} className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">
            <Form.Group>
                <Form.Label>Codice 2FA:</Form.Label>
                <Form.Control type="text" placeholder="2FA" value={code} required={true} onChange={(event) => setCode(event.target.value)} />
            </Form.Group>
            <Button className='mx-2 mt-2' variant="success" type="submit">
                Invia
            </Button>
        </Form>
    );
}


export default MFAForm;