const SERVER_URL = ""//"http://localhost:3001"

async function apiLogin(username, password){ //Try login
    try{
        const data = {username:username, password:password};
        const res = await fetch(SERVER_URL+'/login', {
            credentials: 'include',
            method: 'POST',
            mode: 'cors',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data)
        })
        if(!res.ok){
            return false;
        }
        return true;
    } catch(exception){
        console.error(exception);
    }
}

async function apiIsLoggedIn(){ //Retrieve user's data if logged in
    try{
        const res = await fetch(SERVER_URL+'/me', {
            credentials: 'include'
        })
        return await res.json();
    } catch(exception){
        console.error(exception);
    }
}

async function api2FA(code){
    try{
        const res = await fetch(SERVER_URL+'/2fa?code='+code, {
            credentials: 'include',
        })
        return await res.json();
    } catch(exception){
        console.error(exception);
    }
}

async function apiGetUserData(){
    try{
        const res = await fetch(SERVER_URL+'/user-info', {
            credentials: 'include',
        })
        if(!res.ok){
            return [];
        }
        return await res.json();
    } catch(exception){
        console.error(exception);
    }
}

export {
    apiLogin,
    apiIsLoggedIn,
    api2FA,
    apiGetUserData
}