import { apiGetUserData } from '../api.js';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { useEffect, useState } from 'react';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';

function UserPage(props) {
    const [name, setName] = useState('');
    const [profile, setProfile] = useState('');
    const [secret, setSecret] = useState('');
    const [flag, setFlag] = useState('');

    useEffect(()=>{
        async function getData(){
            const res = await apiGetUserData();
            setName(res.username);
            setProfile(res.picture);
            setSecret(res.secret);
            setFlag(res.flag);
        }
        getData();
    }, [])

    return (<Container className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">
        <Row>
            <Col>
                Benvenuto {name}!
            </Col>
            <Col className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">
            <img src={`${profile}`}></img>
            </Col>

        </Row>
        <Row className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">
            <Col className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">Il tuo QR per l'OTP:
            <img style={{maxWidth:"300px"}} src={`${secret}`}></img></Col>
        </Row>
        <Row className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">
            <Col className="col-12 col-md-8 offset-md-2 col-lg-6 offset-lg-3 my-5">La flag è {flag}</Col>
        </Row>
        </Container>
    );
}


export default UserPage;