import logo from './logo.svg';
import './App.css';
import { Container, Row, Spinner } from 'react-bootstrap';
import { useEffect, useState } from 'react';
import LoginForm from './components/Login/Login.js';
import MFAForm from './components/Login/MFA.js';
import UserPage from './components/UserPage.js'
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";
import { apiIsLoggedIn } from "./api.js"


function App() {
  const [loggedIn, setLoggedIn] = useState(false);
  const [MFAOk, setMFAOk] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    async function checkIfLoggedIn() {
      const res = await apiIsLoggedIn();
      if (res.status == true) {
        setLoggedIn(true);
        setMFAOk(true);
      } else {
        if (res.message == "2FA needed") {
          setLoggedIn(true);
        }
      }
    }
    checkIfLoggedIn()
  }, [])

  function showError(message) {
    toast.error(message, { position: "top-center" }, { toastId: 0 });
  }

  function showSuccess(message) {
    toast.success(message, { position: "top-center" }, { toastId: 0 });
  }

  let content = <></>
  if (!loggedIn) {
    content = <LoginForm setLoggedIn={setLoggedIn} showSuccess={showSuccess} showError={showError} setLoading={setLoading} />
  } else if (!MFAOk) {
    content = <MFAForm setMFAOk={setMFAOk} showSuccess={showSuccess} showError={showError} setLoading={setLoading} />
  } else {
    content = <UserPage setLoading={setLoading} />
  }

  if (loading) {
    return <Container fluid style={{ height: "100vh" }} className="d-flex align-items-center justify-content-center">
      <Row>
        <Spinner animation="border" variant="primary" className="spin-load" size="lg" />
      </Row>
    </Container>;
  }
  else {
    return <>
      <ToastContainer />
      {content}
    </>
  }

}

export default App;
